import { useState, useEffect } from 'react';
import { useLocation, useHistory } from "react-router-dom";

interface UsePageProps {
  countItems: number
  maxItemsOnPage: number
  baseUrl: string
}

interface Indexes {
  begin: number
  end: number
}

const useQuery = () => new URLSearchParams(useLocation().search);

const usePage = (props: UsePageProps) => {
  const query = useQuery();
  const history = useHistory();

  const [indexes, setIndexes] = useState<Indexes | null>(null)
  const [page, setPage] = useState<number>(0)
  const [queryLoaded, setQueryLoaded] = useState<boolean>(false)
  const [countPages, setCountPages] = useState<number>(0)
  const [arrayPages, setArrayPages] = useState<number[]>([])

  useEffect(() => {
    if (query && !queryLoaded) {
      const queryPage = query.get("page")
      const numberPage = Number(queryPage)
      
      setPage(
        !isNaN(numberPage) 
          ? (numberPage > 0 ? numberPage - 1 : numberPage)
          : 0 
      )

      setQueryLoaded(true)
    }
  }, [query, queryLoaded])

  useEffect(() => {
    if (props.countItems) {
      setIndexes(null)
      const countOfPages = props.countItems / props.maxItemsOnPage

      if (page > countOfPages) {
        setPage(0)
        return
      }

      const preparePageEnd = (page * props.maxItemsOnPage) + props.maxItemsOnPage

      const pageBegin = page * props.maxItemsOnPage
      const pageEnd = preparePageEnd > props.countItems ? props.countItems - 1 : preparePageEnd

      const arrayOfPages = []
      for (let i = 0; i < countOfPages; i++) {
        arrayOfPages.push(i + 1)
      }

      setCountPages(Number(countOfPages.toFixed(0)))

      setArrayPages(arrayOfPages)

      setIndexes({
        begin: pageBegin,
        end: pageEnd,
      })
    }
  }, [page, props.maxItemsOnPage, props.countItems])

  const setHistoryPage = (page: number) => {
    history.push(`${props.baseUrl}?page=${page}`)
    setQueryLoaded(false)
  }

  return {
    actions: {
      setPage: setHistoryPage,
    },
    loading: !indexes,
    page,
    indexes,
    countPages,
    arrayPages,
  }
}

export default usePage