import { useEffect, useState } from 'react'
import { useSelector, useDispatch } from "react-redux";
import { RootState } from "../../redux/RootReducer";
import {
  fetchAllProducts,
} from "../../redux/products/actions";
import { Product } from "../types/Types";
// @ts-ignore
import smartSearch from 'smart-search';

interface UseProductsProps {
  name?: string;
  zero?: boolean;
}

const useProducts = (props?: UseProductsProps) => {
  const dispatch = useDispatch();

  const { products } = useSelector((state: RootState) => ({
    products: state.product.products,
  }));

  const [sortProducts, setSortProducts] = useState<Product[]>([])

  useEffect(() => {
    dispatch(fetchAllProducts())
  }, [])

  useEffect(() => {
    setSortProducts([])

    if (props && products) {
      const { name, zero } = props

      if (!name?.length && !zero) {
        setSortProducts(products)
        return
      }

      const filtered = products
        .filter((product: any) => !name?.length 
          ? true 
          : searchByPattern(product.title, name) || searchByPattern(product.title_pt, name))
        .filter((product: any) => !zero ? true : product.storage_count <= 0)

      console.log('new:', filtered.length)

      setSortProducts(filtered)
    }

    if (!props && products) {
      setSortProducts(products)
    }
  }, [props, products])

  const reload = () => dispatch(fetchAllProducts())

  const searchByPattern = (title: string, pattern: string) => { 
    const lTitle = title.toLocaleLowerCase()
    const lPattern = pattern.toLocaleLowerCase()

    let position = []; 
    let found = true; 
 
    for (let i = 0; i < lTitle.length; i++) { 
      found = true; 
 
      for (let j = 0; j < lPattern.length; j++) { 
        if (lTitle[i+j] !== lPattern[j]) { 
          found = false; 
          break; 
        } 
 
      } 
 
      if (found) { 
        position.push(i); 
      } 
    } 
 
    return position.length > 0
  }

  return { 
    loading: typeof products === 'undefined',
    products: sortProducts,
    actions: {
      reload,
    }
  }
}

export default useProducts