import React, { FC, useState } from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import TextField from "@material-ui/core/TextField"
import DialogTitle from "@material-ui/core/DialogTitle";

interface AmountDialogProps {
  title: string;
  show: boolean;
  setShow: React.Dispatch<React.SetStateAction<boolean>>;
  actionOnSuccess: (amount: number) => void;
}

export const AmountDialog: FC<AmountDialogProps> = ({
  title,
  show,
  setShow,
  actionOnSuccess,
}) => {
  const [amount, setAmount] = useState<string>('0')

  return (
    <>
      <Dialog
        open={show}
        onClose={() => setShow(false)}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{title}</DialogTitle>

          <DialogContent>
            <TextField id="standard-basic" label="Amount:" variant="standard" type="number" onChange={e => setAmount(e.target.value)} />
          </DialogContent>
        <DialogActions>
          <Button onClick={() => setShow(false)} color="primary">
            Cancel
          </Button>
          <Button
            onClick={() => {
              actionOnSuccess(Number(amount));
              setShow(false);
            }}
            color="primary"
            autoFocus
          >
            Ok
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};
